import React, { useCallback, useState, useEffect } from "react";
import { TextField, ColorPicker } from "@shopify/polaris";
import axios from "axios";

import "./ColorQuote.css";
export default function TextFieldExample() {
  const [value, setValue] = useState("");
  const [src, setSrc] = useState("");

  const [color, setColor] = useState({
    hue: 120,
    brightness: 1,
    saturation: 1,
    alpha: 1,
  });
  const handleChange = useCallback((newValue) => setValue(newValue), []);

  function rgbaColor(h, s, l, alpha) {
    // s /= 100;
    l /= 2;

    let c = (1 - Math.abs(2 * l - 1)) * s,
      x = c * (1 - Math.abs(((h / 60) % 2) - 1)),
      m = l - c / 2,
      r = 0,
      g = 0,
      b = 0;

    if (0 <= h && h < 60) {
      r = c;
      g = x;
      b = 0;
    } else if (60 <= h && h < 120) {
      r = x;
      g = c;
      b = 0;
    } else if (120 <= h && h < 180) {
      r = 0;
      g = c;
      b = x;
    } else if (180 <= h && h < 240) {
      r = 0;
      g = x;
      b = c;
    } else if (240 <= h && h < 300) {
      r = x;
      g = 0;
      b = c;
    } else if (300 <= h && h < 360) {
      r = c;
      g = 0;
      b = x;
    }
    r = Math.round((r + m) * 255);
    g = Math.round((g + m) * 255);
    b = Math.round((b + m) * 255);

    return "rgba(" + r + "," + g + "," + b + "," + alpha + ")";
  }

  const sendToServer = () => {
    let rgba = rgbaColor(
      color.hue,
      color.saturation,
      color.brightness,
      color.alpha
    );

    console.log(rgba);

    axios
      .post(
        "http://localhost:8080/",
        {
          color: rgba,
          value,
        },
        {
          headers: { "Access-Control-Allow-Origin": "*" },
        }
      )
      .then((response) => {
        // setPost(response.data);
        console.log(response);
        setSrc(response.data.data);
      });

    // const requestOptions = {
    //   method: "GET",
    //   mode: "no-cors",
    //   headers: {
    //     Accept: "application/json",
    //     "Content-Type": "application/json",
    //     "Access-Control-Allow-Origin": "*",
    //   },
    // };
    // fetch("http://localhost:8080/", requestOptions)
    //   .then((response) => {
    //     if (!response.ok) {
    //       throw new Error(`HTTP error ${response.status}`);
    //     }
    //     // *** Read the body, parse it as JSON
    //     return response.json();
    //   })
    //   .then((data) => {
    //     console.log(data);
    //     // setSrc(res.data);
    //   })
    //   .catch((error) => {
    //     console.error("There was an error!", error);
    //   });
  };

  const generateImage = () => {
    console.log(document.querySelector(".main"));
    // console.log("hello");
    // document.querySelector(".main").()

    // html2canvas(`<div id="forImage">
    // <p
    //   style={{

    //     color: ``hsla(${color.hue}deg,${color.saturation * 100}%,${
    //   color.brightness * 50
    // }%,${color.alpha})``,

    //   }}
    //   className="quoteText"
    // >
    //   {value}
    // </p>
    // </div>`).then(function (canvas) {
    //   document.querySelector(".main").append(canvas);

    //   //   document.body.append(canvas);
    // });
    // document.getElementById("forImage").remove();
    // document.getElementById("forImage").removeChild();
  };

  useEffect(() => {
    // generateImage();

    sendToServer();
  }, [value, color]);

  return (
    <div className="main">
      <TextField
        label="Your Quote"
        value={value}
        placeholder="Your Quote"
        onChange={handleChange}
        autoComplete="off"
      />

      <ColorPicker
        allowAlpha={true}
        onChange={(color) => {
          console.log(color);
          setColor(color);
        }}
        color={color}
      />

      <div id="forImage">
        <img src={src} />
      </div>
    </div>
  );
}
